import { Util } from '@/config/util';

export const getCategory = function(data,callback){
	Util.DataEncSubmit('category_get.php',data,(resp)=>{
		if (callback && typeof callback === "function") {
            callback(resp);
        }
	});
};

export const getPrinter = function(data,callback){
	Util.DataEncSubmit('printer_get.php',data,(resp)=>{
		if (callback && typeof callback === "function") {
            callback(resp);
        }
	});
};

export const savePrinter = function(data,callback){
	Util.DataEncSubmit('printer_save.php',data,(resp)=>{
		if (callback && typeof callback === "function") {
            callback(resp);
        }
	});
};

export const getPrintID = function (data, callback) {
    Util.DataEncSubmit('gen_id.php', data, (resp) => {
        if (callback && typeof callback === "function") {
            callback(resp);
        }
    });
};

export const Delprinter = function(data,callback){
	 Util.DataEncSubmit('printer_save.php', data, (resp) => {
        if (callback && typeof callback === "function") {
            callback(resp);
        }
    });
};



