<template>
	<div id="printers">
		<!--头部按钮组 start-->
		<div class="btn-group">
			<router-link :to="{path:'/printer/edit'}">
				<button type="button" class="add">添加</button>
			</router-link>
			<button type="button" class="del" @click="multipleDeletePrinter">删除</button>
		</div>
		<!--头部按钮组 end-->
		<!--打印机列表 start-->
		<div class="printer-table">
			<el-table border style="width: 100%"  stripe :data='printList' height="680" @selection-change="handleSelectionChange" @select="selectRow">
				<el-table-column type="selection" min-width="52">
				</el-table-column>
				<el-table-column label="打印机ID" align="center" min-width="180">
					<template slot-scope="scope">
						<span>{{ scope.row.printer_id}}</span>
					</template>
				</el-table-column>
				<el-table-column label="名称" align="center" min-width="180">
					<template slot-scope="scope">
						<span>{{ scope.row.printer_name}}</span>
					</template>
				</el-table-column>
				<el-table-column label="支持单据" align="center" min-width="300">
					<template slot-scope="scope">
						<div class="types">
							<span v-for='(item,index) in scope.row.printer_category'>{{docType[item]}}<i v-if="index < scope.row.printer_category.length-1"> / </i></span>
						</div>
					</template>
				</el-table-column>
				<el-table-column label="后厨打印分类" align="center" min-width="250">
					<template slot-scope="scope">
						<span v-for='(item,index) in scope.row.food_category_name'>{{item}}<i v-if="index < scope.row.food_category_name.length-1"> / </i></span>
					</template>
				</el-table-column>
				<el-table-column label="规格" align="center" min-width="180">
					<template slot-scope="scope">
						<span>{{ scope.row.printer_size}}mm</span>
					</template>
				</el-table-column>
				<el-table-column label="型号" align="center" min-width="180">
					<template slot-scope="scope">
						<span>{{ scope.row.printer_brand}}</span>
					</template>
				</el-table-column>
				<el-table-column label="备注" align="center" min-width="180">
					<template slot-scope="scope">
						<span>{{ scope.row.printer_note}}</span>
					</template>
				</el-table-column>
				<el-table-column label="操作" min-width="180" align="center">
					<template slot-scope="scope">
						<router-link :to="{path:'/printer/edit',query:{id:scope.row.printer_id}}">
							<span size="small" type="text" class="blue">编辑
							</span>
						</router-link>
						<span size="small" type="text" class="red" @click='delPrinter(scope.row.printer_id)'>删除
						</span>
					</template>
				</el-table-column>
			</el-table>
			<!-- 分页 starat -->
			<div class="pagination-container" ref='page'>
				<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="listQuery.page" :page-sizes="[10,20,40]" :page-size="listQuery.limit" layout="sizes, jumper, prev, pager, next" :total='total'>
				</el-pagination>
			</div>
			<!-- 分页 end -->
		</div>
		<!--打印机列表 end-->
		<!--删除打印机模态框 start-->
		<div class="mask" v-show="showDialog"></div>
		<div class="dialog" v-show="showDialog">
			<div class="header">
				<p class="text">确定删除打印机<span v-for='(print,index) in multipleSelection'>{{print.printer_id}}<i v-if="index < multipleSelection.length-1 ">,</i> </span>的信息？</p>
			</div>
			<div class="bottom">
				<button type="button" class="primary ttt" @click="Confim">确定</button>
				<button type="button" class="default" @click="close">取消</button>
			</div>
		</div>
		<!--删除打印机模态框 end-->
		<!--保存成功失败 Tips start-->
		<div class="tips" v-if='showTips'>
			<span class="info">{{msg}}</span>
		</div>
		<!--保存成功失败 Tips end-->
		<!--暂无数据样式 start-->
		<div class="empty" v-if='showEmpty'>
			<div class="imgs">
				<img src="./img/empty.png" alt="" />
			</div>
			<span>暂无数据...</span>
		</div>
		<!--暂无数据样式 end-->
	</div>
</template>

<script>
import { documentsType } from "@/config/cfg";
import { getPrinter, Delprinter } from "../api";
import { Comethods } from "../index";
import { ls } from "@/config/pageStore";
export default {
  data() {
    return {
      printList: [], //列表显示的数据
      testList: [], //用于分页list过滤
      docType: "", //支持单据类型
      total: 40, //总数
      listQuery: {
        page: 1,
        limit: 10
      },
      multipleSelection: [], //存放多选选中数据
      selectIndex: {}, //记录某页的勾选项
      showDialog: false, //控制显示删除打印机模态框
      showTips: false, //控制显示Tips提示框
      msg: "", //提示框提示内容
      showEmpty: false //无数据显示
    };
  },
  created() {
    this.initTableData();
  },
  computed: {
    documentsType() {
      return documentsType;
    }
  },
  methods: {
    //初始化表格信息
    initTableData() {
      //获取每页选择显示的条目数的历史记录
      if (ls.getItem("itemNum") != null) {
        this.listQuery.limit = Number(ls.getItem("itemNum"));
      }
      //获取每页选择页面的历史记录
      if (sessionStorage.getItem("pageNum") != null) {
        this.listQuery.page = Number(sessionStorage.getItem("pageNum"));
      }
      getPrinter(
        {
          printer_list: 1
        },
        resp => {
          this.printList = resp.data.list || [];
          let page = this.$refs.page;
          if (this.printList.length === 0) {
            this.showEmpty = true;
            page.style.display = "none";
          } else {
            page.style.display = "block";
          }
          this.testList = resp.data.list || [];
          this.total = Comethods.getTotal(this.testList);
          this.printList = Comethods.getPageList(
            this.testList,
            this.listQuery.limit,
            this.listQuery.page
          );
          this.docType = documentsType.code;
        }
      );
    },

    //单项删除
    delPrinter(id) {
      let printArr = [];
      printArr.push(id);
      let data = {
        del_printer: 1,
        printer_id_list: printArr
      };
      Delprinter(data, this.opearteHandler);
    },
    //删除成功后的处理
    opearteHandler(resp) {
      if (resp.ret === 0) {
        this.showTips = true;
        this.msg = "删除成功";
        this.initTableData();
        setTimeout(() => {
          this.showTips = false;
        }, 1500);
      } else {
        this.showTips = true;
        this.msg = "删除失败";
        setTimeout(() => {
          this.showTips = false;
        }, 1500);
      }
    },
    //分页事件处理，条数和当前页数改变触发
    handleSizeChange(val) {
      this.listQuery.limit = val;
      ls.setItem("itemNum", this.listQuery.limit);
      this.printList = Comethods.getPageList(
        this.testList,
        this.listQuery.limit,
        this.listQuery.page
      );
      this.total = Comethods.getTotal(this.testList);
    },
    //目标页
    handleCurrentChange(val) {
      this.listQuery.page = val;
      this.total = Comethods.getTotal(this.testList);
      this.printList = Comethods.getPageList(
        this.testList,
        this.listQuery.limit,
        this.listQuery.page
      );
      let allSelectIndex = this.selectIndex[val];
      let indexArr = [];
      if (allSelectIndex !== undefined) {
        for (let i in this.selectIndex[val]) {
          let index = Number(this.selectIndex[val][i] + 1);
          $(".el-checkbox__input")
            .eq(index)
            .addClass("is-checked");
        }
      }
      sessionStorage.setItem("pageNum", this.listQuery.page);
    },
    //批量删除
    multipleDeletePrinter() {
      if (this.multipleSelection.length === 0) {
        this.showTips = true;
        this.msg = "未选中任何消息";
        setTimeout(() => {
          this.showTips = false;
        }, 1500);

        return;
      }
      this.showDialog = true;
    },
    //选择每一行，记录页面上面的选中项
    selectRow(selection, row) {
      var arr = [];
      this.testList.forEach((r, index) => {
        for (var i = 0; i < this.testList.length; i++) {
          if (r == selection[i]) {
            arr.push(index);
          }
        }
      });
      this.selectIndex[this.listQuery.page] = arr;
    },
    //多选
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    //获取选择列表的ID
    getSelectedIds() {
      let array = [];
      this.multipleSelection.forEach(res => {
        array.push(res.printer_id);
      });
      return array;
    },
    //多选删除
    Confim() {
      this.showDialog = false;
      let printArr = this.multipleSelection.map(function(item) {
        return item.printer_id;
      });
      let data = {
        del_printer: 1,
        printer_id_list: printArr
      };
      Delprinter(data, this.opearteHandler);
    },
    //关闭删除模态框
    close() {
      this.showDialog = false;
    }
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";
#printers {
  position: relative;
}
button {
  cursor: pointer;
}
.empty {
  position: absolute;
  left: 0;
  top: 120px;
  width: 100%;
  height: 610px;
  background: #fff;
  text-align: center;
  padding-top: 189px;
  span {
    @include fc(14px,#9b9b9b);
  }
}
.tips {
  position: fixed;
  @include wh(360px, 100px);
  background: #000;
  z-index: 1002;
  opacity: 0.8;
  left: 50%;
  top: 50%;
  margin-left: -180px;
  margin-top: -50px;
  text-align: center;
  border-radius: 2px;
  .info {
    display: inline-block;
    @include fc(14px, #fff);
    padding-top: 40px;
    letter-spacing: 0.5;
  }
}
.btn-group {
  padding: 20px 0;
  background: #fff;
  text-align: right;
  button {
    cursor: pointer;
  }
  .add {
    @include wh(100px, 34px);
    @include fc(14px, #4877e7);
    background: #fff;
    border: 1px solid #4877e7;
    margin-right: 9px;
  }
  .del {
    @include wh(100px, 34px);
    @include fc(14px, #E7487E);
    border: 1px solid #e7487e;
    background: #fff;
  }
}

.blue {
  font-size: 14px;
  color: #4877e7;
  margin-right: 6px;
  cursor: pointer;
}

.red {
  font-size: 14px;
  color: #e7487e;
  cursor: pointer;
}

.printer-table {
  background: #fff;
  height: 100%;
}

.types {
  text-align: center;
}

.mask {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 8000;
  left: 0;
  top: 0;
  background: #000000;
  opacity: 0.5;
}

.dialog {
  position: absolute;
  z-index: 10000000;
  left: 50%;
  top: 50%;
  border-top: 4px solid #5a8cff;
  width: 540px;
  height: 280px;
  margin-top: -140px;
  margin-left: -270px;
  background: #ffffff;
  text-align: center;
  .header {
    margin-top: 90px;
    .text {
      @include fc(14px, #666);
    }
  }
  .bottom {
    padding: 77px 0 20px 0;
    .primary {
      display: inline-block;
      background: $blue;
      @include wh(160px, 40px);
      line-height: 40px;
      color: #fff;
      border: 1px solid $blue;
      border-radius: 5px;
      cursor: pointer;
    }
    .default {
      @include fc(14px, #4877e7);
      background: #ffffff;
      @include wh(160px, 40px);
      line-height: 40px;
      border-radius: 6px;
      border: 1px solid #4877e7;
      margin-left: 50px;
    }
  }
}
</style>
<style lang="scss">
@import "src/styles/mixin.scss";
#printers {
  .el-table tbody tr {
    height: 60px;
  }
  .el-table .cell,
  .el-table th > div {
    padding: 0 8px 0 8px;
  }
  .pagination-container {
    display: none;
    text-align: center;
    padding: 50px 14px 20px;
  }
  .el-table thead .cell {
    font-weight: 900 !important;
  }
  .el-table__empty-block {
    display: none;
  }
  .el-table__empty-text {
    display: none;
  }
  .el-table td {
    height: 80px;
  }
}
</style>


