export const Comethods = {
	//获取总的条目数
	getTotal:function(testList) {
		if(testList!=undefined &&testList.length>0){
			 var tot = 0;
			    for (var i =0 ;i<testList.length;i++){
			    	tot++;
			    }
			     return tot;
		}
				
	},
	//获取每页显示的相对应条目的内容
	getPageList:function(testList,limit,page){
		if(testList!=undefined &&testList.length>0){
			var printList =[];
			 printList=testList.filter((item, index) => index <limit * page && index >= limit * (page - 1));
			 return printList;
		 }
	},
	unique:function(arr){
		var newArr = [arr[0]];
  　　 for(var i=1;i<arr.length;i++){
　　　　if(newArr.indexOf(arr[i]) == -1){
          　　 newArr.push(arr[i]);
    　　  }
        }
        return newArr;
	}
};



